.clients_family,
.edit_client {
  h6 {
    font-family: "Helvetica";
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 125.99%;
    color: #000000;
  }
  p{
    font-size: 14px;
  }
}
.client_box {
  background: #ffffff;
  box-shadow: 0px 2.42667px 4.85333px rgba(216, 220, 230, 0.52);
  border-radius: 12px;
  label {
    font-family: "Helvetica";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #000000;
  }
}
.date_of_birth {
  width: 100% !important;
  .react-datepicker-wrapper {
    width: 100%;
  }
}
